export default {
	data() {
		return {
			userInfo: [],
			avator: ''
		}
	},
	methods: {
		appLoginWx() {
			var that = this
			uni.getProvider({
				service: 'oauth',
				success: function(res) {
					if (~res.provider.indexOf('weixin')) {
						uni.login({
							provider: 'weixin',
							success: (res2) => {
								uni.getUserInfo({
									provider: 'weixin',
									success: (info) => {
										console.log(res2);
										console.log(info);
										that.userInfo = info.userInfo
										that.avator = info.userInfo.avatarUrl
										uni.setStorage({
											key: 'userInfo',
											data: info.userInfo,
											success: function() {
												console.log('setStorage success');
											}
										});
										uni.request({
											url: 'https://api-hmugo-web.itheima.net/api/public/v1/users/wxlogin', //仅为示例，并非真实接口地址。
											data: {
												encryptedData: info.encryptedData,
												rawData: info.rawData,
												iv: info.iv,
												signature: info.signature,
												code: res2.code
											},
											success: (res) => {
												console.log(res);
											}
										});

									},
									fail: () => {
										uni.showToast({
											title: "微信登录授权失败",
											icon: "none"
										});
									}
								})

							},
							fail: () => {
								uni.showToast({
									title: "微信登录授权失败",
									icon: "none"
								});
							}
						})

					} else {
						uni.showToast({
							title: '请先安装微信或升级版本',
							icon: "none"
						});
					}
				}
			});
		}
	},
	created() {
		var that = this
		uni.getStorage({
			key: 'userInfo',
			success: function(res) {
				console.log(res.data);
				that.userInfo = res.data
				that.avator = res.data.avatarUrl
			},
			fail() {
				that.appLoginWx()
			}
		});
	},
	mounted() {}

}
